<template>
    <div class="content">
        <div class="search" style="height:60rpx;">
            <input @input="bindKeyInput" type="text" placeholder="搜索歌曲" class="inputSearch">
            <i @click="searchI" style="background:url(data:image/svg+xml;base64,); background-size:56rpx 56rpx; width:56rpx;height:56rpx;"></i>
        </div>
        <div class="banner">
            <swiper class="bannerSwiper" :indicator-dots="true" :autoplay="true" :circular="true" :interval="5000" :duration="500" indicator-active-color="#7054dd">
                <block v-for="item in banner" :key="item.id">
                    <swiper-item class="bannerImg">
                        <image :src="item.pic" class="slide-image" />
                    </swiper-item>
                </block>
            </swiper>
        </div>
        <div class="Entrance">
            <div class="EntranceIcon">
                <img src="data:image/svg+xml;base64,">
                <p>电台</p>
            </div>
            <div class="EntranceIcon" @click="moreSongsList">
                <img src="data:image/svg+xml;base64,">
                <p>歌单</p>
            </div>
            <div class="EntranceIcon" @click="ranking">
                <img src="data:image/svg+xml;base64,">
                <p>排行榜</p>
            </div>
        </div>
        <div class="songList">
            <div class="songTop">今日歌单
                <i style="background:url(data:image/svg+xml;base64,); background-size:24rpx 24rpx; width:24rpx;height:24rpx;"></i>
            </div>
            <div class="songitem">
                <div v-for="(item,index) in songitem" :key="item.id" v-if="index<=5" class="songWidth" @click="songItem(item.id)">
                    <div class="songImg">
                        <img :src="item.picUrl" alt="">
                        <p>
                            <i></i>{{item.playCount}}</p>
                    </div>
                    <p>{{item.name}}</p>
                </div>
            </div>
        </div>
        <div class="songList" style="margin-top:20rpx">
            <div class="songTop">今日单曲
                <i style="background:url(data:image/svg+xml;base64,); background-size:24rpx 24rpx; width:24rpx;height:24rpx;"></i>
            </div>
            <div class="Single">
                <div v-for="(item,index) in Single" :key="item.id" v-if="index<=4" class="SingleWidth">
                    <div @click="musicUrl(item.id)">
                        <img :src="item.song.album.picUrl" alt="">
                        <div>
                            <p class="name">{{item.name}}</p>
                            <div class="people">
                                <p v-for="(itempeo,indexa) in item.song.album.artists" :key="itempeo.id" v-if="indexa<=1">
                                    {{itempeo.name}} &nbsp;
                                </p>
                            </div>
                        </div>
                    </div>
                    <i @click="JoinTheList(item.id)" style="background:url(data:image/svg+xml;base64,); background-size:40rpx 40rpx; width:40rpx;height:40rpx;"></i>
                </div>
            </div>
        </div>
        <div class="songList">
            <div class="songTop">今日电台
                <i style="background:url(data:image/svg+xml;base64,); background-size:24rpx 24rpx; width:24rpx;height:24rpx;"></i>
            </div>
            <div class="songitem">
                <div v-for="(item,index) in Radio" :key="item.id" v-if="index<=2" class="songWidth">
                    <div class="songImg" style="position: relative;">
                        <img :src="item.picUrl" alt="" style="opacity:0.9">
                        <i style="background:url(data:image/svg+xml;base64,) no-repeat; background-size:80rpx 80rpx; width:210rpx;height:210rpx;position: absolute;left:0;top: 0;background-color:rgba(0,0,0,.2);background-position:center;"></i>
                    </div>
                    <p>{{item.name}}</p>
                </div>
            </div>
        </div>
        <div class="Playing" @click="listenerButtonPause">
            <div class='wrap'>
                <i style="background:url(data:image/svg+xml;base64,); background-size:80rpx 80rpx; width:80rpx;height:80rpx;margin-top:20rpx;margin-left:20rpx"></i>
            </div>

        </div>
    </div>
</template>

<script>
import request from '@/utils/request'

export default {
    data() {
        return {
            banner: {},
            autoplay: true,
            songitem: {},
            Single: {},
            Radio: {}
        };
    },
    created() {
        if (wx.getStorageSync("playing")) {
        } else {
            wx.setStorageSync("playing", []);
        }

        request
            .get("banner", {})
            .then(d => {
                
                this.banner = d.data.banners;
            })
            .catch(err => {
                console.log(err.status, err.message);
            });
        request
            .get("personalized", {})
            .then(d => {
                
                console.log(d)
                this.songitem = d.data.result;
            })
            .catch(err => {
                console.log(err.status, err.message);
            });
        request
            .get("http://localhost:3000/personalized/newsong", {})
            .then(d => {
                  wx.hideLoading();
                this.Single = d.data.result;
            })
            .catch(err => {
                console.log(err.status, err.message);
            });
        request
            .get("http://localhost:3000/personalized/djprogram", {})
            .then(d => {
                this.Radio = d.data.result;
            })
            .catch(err => {
                console.log(err.status, err.message);
            });
    },
    methods: {
        musicUrl(e) {
            const url = "../music/main?id=" + e;
            wx.navigateTo({ url });
        },
        //监听button暂停按钮
        listenerButtonPause: function() {
            let e = wx.getStorageSync("musicId");

            if (!e) {
            } else {
                const url = "../music/main?id=" + e;
                wx.navigateTo({ url });
            }
        },
        bindKeyInput: function(e) {
            this.bindKeyInput = e.target.value;
        },
        searchI: function() {
            let searchValue = this.bindKeyInput;

            const url = "../search/main?search=" + searchValue;
            wx.navigateTo({ url });
        },
        ranking: function() {
            const url = "../ranking/main";
            wx.navigateTo({ url });
        },
        moreSongsList: function() {
            const url = "../MoreSongList/main";
            wx.navigateTo({ url });
        },
        songItem: function(e) {
            const url = "../songItem/main?id=" + e;
            wx.navigateTo({ url });
        },
        JoinTheList: function(e) {
            let chongfu = [];
            let playing = wx.getStorageSync("playing");
            for (let index = 0; index < playing.length; index++) {
                const element = playing[index].id;
                chongfu.push(element)
            }
            for (let index = 0; index < chongfu.length; index++) {
                const elementq = chongfu[index];
                if (elementq == e) {
                    var panduanchongfu = true
                }
            }
            if (!panduanchongfu) {
                let status =  {
                    id: e,
                    status: false
                }
                playing.unshift(status);
                wx.setStorageSync("playing", playing);
            }
        }
    }
};
</script>

<style scoped lang="scss">
.content {
    display: flex;
    flex-direction: column;
    background: #fff;
    padding: 0 40rpx;

    .banner {
    margin-top: 20rpx;
    width: 670rpx;
    height: 260rpx;
    box-shadow: 0 16rpx 8rpx -8rpx rgba(0, 0, 0, 0.4);
    border-radius: 10rpx;
    overflow: hidden;
    }
}

.bannerSwiper {
    height: 260rpx;
}
.bannerImg {
    min-height: 260rpx;
}
.bannerImg image {
    min-width: 100%;
    height: 260rpx;
}
.Entrance {
    padding: 40rpx 60rpx 0;
    width: 550rpx;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.EntranceIcon {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    overflow: hidden;
}
.EntranceIcon p {
    font-size: 24rpx;
}
.EntranceIcon img {
    width: 72rpx;
    height: 72rpx;
}
.songList {
    margin-top: 40rpx;
    padding-top: 20rpx;
    border-top: 1px solid #f3f3f3;
}
.songTop {
    text-align: center;
    font-size: 32rpx;
    position: relative;
}
.songTop i {
    position: absolute;
    right: 0;
    top: 50%;
    margin-top: -12rpx;
}
.songitem {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 30rpx;
}
.songWidth {
    width: 210rpx;
    margin-bottom: 30rpx;
}
.songWidth > p {
    padding-top: 10rpx;
    font-size: 24rpx;
    overflow: hidden;
    height: 68rpx;
}
.songImg {
    width: 210rpx;
    height: 210rpx;
    position: relative;
}
.songImg > p {
    width: 200rpx;
    font-size: 20rpx;
    background: rgba(0, 0, 0, 0.4);
    position: absolute;
    bottom: 0;
    right: 0;
    color: #fff;
    text-align: right;
    padding-right: 10rpx;

    border-radius: 0 0 10rpx 10rpx;
}
.songImg img {
    width: 210rpx;
    height: 210rpx;
    border-radius: 10rpx;
}
.footer {
    position: fixed;
    bottom: 0;
    width: 750rpx;
    height: 100rpx;
    background-color: #7054dd;
}
.Single {
    margin-top: 30rpx;
}
.SingleWidth {
    width: 670rpx;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
.SingleWidth > div {
    display: flex;
    margin-bottom: 30rpx;
}
.SingleWidth > div img {
    width: 120rpx;
    height: 120rpx;
    border-radius: 10rpx;
}
.SingleWidth > div > div {
    margin-left: 20rpx;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.name {
    font-size: 32rpx;
}
.people {
    margin-top: 10rpx;
    font-size: 24rpx;
    display: flex;
    color: #999;
    max-width: 420rpx;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.Playing {
    position: fixed;
    bottom: 20rpx;
    right: 40rpx;
    width: 120rpx;
    height: 120rpx;
    background: #fff;
    border: 2px solid #7054dd;
    border-radius: 50%;
    overflow: hidden;
}
.search {
    display: flex;
}
.inputSearch {
    flex: 1;
    margin-right: 20rpx;
    border-radius: 60rpx;
    border: 1px solid #7054dd;
    font-size: 24rpx;
    padding-left: 20rpx;
}
</style>



